import React, { useCallback, useEffect, useState } from "react";
import { signBack, signBg, signFront, star } from "../../../../assets/images";
import "./home.slider.scss";

const data = [
  {
    x: "0",
    z: "0",
    scale: "2, 1.8",
    content: [
      "5000+ questions to practice",
      "Reliable study material",
      "Progressive Learning",
    ],
    title: 'Customized Practice Ground'
  },
  {
    x: "-120%",
    z: "-150px",
    scale: "1",
    content: [
      "5000+ questions to practice",
      "Reliable study material",
      "Progressive Learning",
    ],
    title: 'Question Bank'
  },
  {
    x: "120%",
    z: "-150px",
    scale: "1",
    content: [
      "5000+ questions to practice",
      "Reliable study material",
      "Progressive Learning",
    ],
    title: 'Applying Data Analytics'
  },
];


const Carousel = () => {
  const [transform, setTransform] = useState(data);
  const [item, setItems] = useState([]);

  const slider = (data: any, i: number) => {
    const isActive = data.x === "0";
    return (
      <>
        {isActive ? <div className="title">{data?.title}</div> : null }
        <div
          key={i}
          className={`card ${isActive ? "active" : ""}`}
          style={{
            transform: `translateX(${data.x}) translateZ(${data.z}) scale(${data.scale})`,
            backgroundColor: isActive ? "#5858C6" : "white",
            zIndex: isActive ? 99 : 0,
          }}
        >
          <div className="dot-container">
            {Array(isActive ? 9 : 6)
              .fill(0)
              .map(() => (
                <span className="dot"></span>
              ))}
          </div>
          <ul className="slider-content">
            {isActive
              ? data?.content?.map((val: any) => (
                  <li>
                    <img src={star} alt={val} className="star" />
                    <div>{val}</div>
                  </li>
                ))
              : [1, 2, 3, 4].map((i) => <li></li>)}
          </ul>
        </div>
        {isActive ? (
          <div className="explore-btn">
            <div className="tail"></div>
            <div className="main">Explore more</div>
            <div className="head"></div>
          </div>
        ) : null}
      </>
    );
  };

  const next = useCallback(() => {
    const transforms = [...transform];
    transforms.push((transforms as any).shift());
    setTransform(transforms);
  }, [transform]);

  useEffect(() => {
    const rendered: any = transform.map(slider);
    setItems(rendered);

    const timer = setInterval(() => {
      next();
    }, 2500);

    return () => {
      clearInterval(timer);
    };
  }, [next, transform]);

  return (
    <>
      <div id="carousel">
        {item}
      </div>
      <div className="indicators">
        {transform.map((val, i) => (
          <div className={`dot ${val.x === '0' ? 'active' : ''}`}></div>
        ))}
      </div>
    </>
  );
};

export default Carousel;
